import { useEffect } from 'react';
import { UIElementStatus } from '@adyen/adyen-web/dist/types/components/types';
import { PaymentOptionName } from 'configurations/paymentOptions';

interface PaymentComponentStatusEventDetail {
  paymentMethod: PaymentOptionName;
  status: UIElementStatus;
}

export interface PaymentComponentStatusEvent extends CustomEvent {
  detail: PaymentComponentStatusEventDetail;
}

export const usePaymentComponentStatusEvent = (
  targetPaymentMethod: PaymentOptionName,
  callback: (event: PaymentComponentStatusEvent) => void
) => {
  useEffect(() => {
    const handler = (event: PaymentComponentStatusEvent | Event) => {
      const { paymentMethod, status } = (event as PaymentComponentStatusEvent).detail;

      if (targetPaymentMethod !== paymentMethod) {
        return;
      }

      if (!status) {
        return;
      }

      callback(event as PaymentComponentStatusEvent);
    };

    window.addEventListener('payment-component-status-event', handler);

    return () => {
      window.removeEventListener('payment-component-status-event', handler);
    };
  }, [targetPaymentMethod, callback]);
};
