import Core from '@adyen/adyen-web/dist/types/core';
import { AdyenPaymentMethodType } from 'configurations/adyenPaymentMethods';
import { PaymentOptionId } from 'configurations/paymentOptions';
import { ClientConfig } from 'types/config';
import { PaymentOption } from 'types/paymentOptions';

export const isClientConfigValid = (config: ClientConfig): boolean => {
  if (!config.environment) {
    return false;
  }

  if (!config.clientKey) {
    return false;
  }

  if (!config.onSubmit) {
    return false;
  }

  return true;
};

export const isPaymentMethodEnabled = (
  availablePaymentOptions: PaymentOption[],
  paymentOptionId: PaymentOptionId
): boolean => {
  if (!availablePaymentOptions) {
    return false;
  }

  return availablePaymentOptions.find(option => option.id === paymentOptionId) !== undefined;
};

/**
 * Checks if specific payment method is available for using, based on the current AdyenCheckout configuration.
 */
export const isAdyenPaymentMethodAvailable = (
  checkout: Core,
  adyenPaymentMethodType: AdyenPaymentMethodType
): boolean => {
  return checkout.paymentMethodsResponse.has(adyenPaymentMethodType);
};
