import { useEffect, useRef, useState } from 'react';
import { Accordion, AccordionSummary } from '@mui/material';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import PaypalElement from '@adyen/adyen-web/dist/types/components/PayPal';
import { PaymentMethodProps } from 'types/components';
import { PaymentOptionId, PaymentOptionName } from 'configurations/paymentOptions';
import { isAdyenPaymentMethodAvailable, isPaymentMethodEnabled } from 'utils/validation';
import { AdyenPaymentMethodType } from 'configurations/adyenPaymentMethods';
import { PaymentActionEvent, usePaymentActionEvent } from 'hooks/usePaymentActionEvent';
import { MethodHeader, MethodNote, MethodBody, MethodButtonContainer } from 'components/MethodUi';
import { usePaymentContext } from 'hooks/paymentContext';
import {
  PaymentComponentStatusEvent,
  usePaymentComponentStatusEvent
} from 'hooks/usePaymentComponentStatusEvent';

const PayPal = ({ onAccordionChange }: PaymentMethodProps): JSX.Element | null => {
  const { checkout, paymentOptions, selectedMethod } = usePaymentContext();
  const componentRef = useRef<HTMLDivElement>(null);
  const [isEnabled, setIsEnabled] = useState(true);
  const [component, setComponent] = useState<PaypalElement>();

  useEffect(() => {
    const isMethodReady =
      isAdyenPaymentMethodAvailable(checkout, AdyenPaymentMethodType.PayPal) &&
      isPaymentMethodEnabled(paymentOptions, PaymentOptionId.PayPal);

    setIsEnabled(isMethodReady);

    if (isMethodReady && componentRef.current) {
      const element = checkout.create('paypal').mount(componentRef.current);
      setComponent(element);
    }
  }, [checkout, paymentOptions]);

  const handlePaymentActionEvent = (event: PaymentActionEvent) => {
    const { action } = event.detail;

    if (!(component && isEnabled)) {
      return;
    }

    component.handleAction(action);
  };

  const handleComponentStatusEvent = () => {
    if (!(componentRef.current && isEnabled && component)) {
      return;
    }

    component.unmount();

    const element = checkout.create('paypal').mount(componentRef.current);
    setComponent(element);
  };

  usePaymentActionEvent(PaymentOptionName.PayPal, handlePaymentActionEvent);
  usePaymentComponentStatusEvent(PaymentOptionName.PayPal, handleComponentStatusEvent);

  if (!isEnabled) {
    return null;
  }

  const isExpanded = selectedMethod === PaymentOptionName.PayPal;

  return (
    <Accordion expanded={isExpanded} onChange={() => onAccordionChange(PaymentOptionName.PayPal)}>
      <AccordionSummary
        expandIcon={<ExpandMoreIcon />}
        aria-controls="payment-paypal-content"
        id="payment-paypal-header"
      >
        <MethodHeader>PayPal</MethodHeader>
      </AccordionSummary>
      <MethodBody>
        <MethodNote>You will be forwarded to the PayPal portal to proceed with payment.</MethodNote>
        <MethodButtonContainer>
          <div ref={componentRef}></div>
        </MethodButtonContainer>
      </MethodBody>
    </Accordion>
  );
};

export default PayPal;
