import { useEffect } from 'react';
import { PaymentAction } from '@adyen/adyen-web/dist/types/types';
import { PaymentOptionName } from 'configurations/paymentOptions';

interface PaymentActionEventDetail {
  paymentMethod: PaymentOptionName;
  action: PaymentAction;
}

export interface PaymentActionEvent extends CustomEvent {
  detail: PaymentActionEventDetail;
}

export const usePaymentActionEvent = (
  targetPaymentMethod: PaymentOptionName,
  callback: (event: PaymentActionEvent) => void
) => {
  useEffect(() => {
    const handler = (event: PaymentActionEvent | Event) => {
      const { paymentMethod, action } = (event as PaymentActionEvent).detail;

      if (targetPaymentMethod !== paymentMethod) {
        return;
      }

      if (!action) {
        return;
      }

      callback(event as PaymentActionEvent);
    };

    window.addEventListener('payment-action-event', handler);

    return () => {
      window.removeEventListener('payment-action-event', handler);
    };
  }, [targetPaymentMethod, callback]);
};
