import { UIElementStatus } from '@adyen/adyen-web/dist/types/components/types';
import { PaymentOptionName } from 'configurations/paymentOptions';

const setComponentState = (paymentMethod: PaymentOptionName, status: UIElementStatus) => {
  const paymentComponentStatusEvent = new CustomEvent('payment-component-status-event', {
    detail: {
      paymentMethod,
      status
    }
  });

  window.dispatchEvent(paymentComponentStatusEvent);
};

export default setComponentState;
