import { PaymentAction } from '@adyen/adyen-web/dist/types/types';
import { PaymentOptionName } from 'configurations/paymentOptions';
import handlePaymentAction from './handlePaymentAction';
import setComponentState from './setComponentState';

const clientActions = {
  onPaymentAction: (paymentMethod: PaymentOptionName, action: PaymentAction) => {
    handlePaymentAction(paymentMethod, action);
  },
  onPaymentError: (paymentMethod: PaymentOptionName) => {
    setComponentState(paymentMethod, 'ready');
  }
};

export default clientActions;
