import { PaymentAction } from '@adyen/adyen-web/dist/types/types';
import { PaymentOptionName } from 'configurations/paymentOptions';

const handlePaymentAction = (paymentMethod: PaymentOptionName, action: PaymentAction) => {
  const paymentActionEvent = new CustomEvent('payment-action-event', {
    detail: {
      paymentMethod,
      action
    }
  });

  window.dispatchEvent(paymentActionEvent);
};

export default handlePaymentAction;
