import { CoreOptions } from '@adyen/adyen-web/dist/types/core/types';
import AdyenCheckout from '@adyen/adyen-web';
import Core from '@adyen/adyen-web/dist/types/core';
import { CheckoutSession } from '@adyen/adyen-web/dist/types/types';
import { ClientConfig } from 'types/config';
import { isClientConfigValid } from 'utils/validation';
import { PaymentClientError } from 'utils/errors';
import { derivePaymentOptionFromState } from 'utils/paymentOptions';

const paymentMethodsConfiguration = {
  card: {
    brands: ['mc', 'visa', 'amex', 'jcb']
  },
  paypal: {
    blockPayPalCreditButton: true,
    blockPayPalPayLaterButton: true
  }
};

export const createCheckout = async (
  session: CheckoutSession,
  config: ClientConfig
): Promise<Core> => {
  const { environment, clientKey, onSubmit, onError, onPaymentCompleted, onAdditionalDetails } =
    config;

  if (!isClientConfigValid(config)) {
    throw new PaymentClientError('Provided configuration is not valid');
  }

  const configuration: CoreOptions = {
    environment,
    clientKey,
    session,
    showPayButton: true,
    analytics: {
      enabled: false
    },
    paymentMethodsConfiguration,
    onSubmit: (state: unknown, component: unknown) => {
      const paymentMethod = derivePaymentOptionFromState(state);
      onSubmit(state, component, paymentMethod);
    },
    onAdditionalDetails: (state: unknown, component: unknown) => {
      onAdditionalDetails(state, component);
    },
    onError: (error: unknown, component: unknown) => {
      onError(error, component);
    },
    onPaymentCompleted: (result: unknown, component: unknown) => {
      onPaymentCompleted(result, component);
    }
  };

  return await AdyenCheckout(configuration);
};
