import { AdyenPaymentMethodType } from './adyenPaymentMethods';

export enum PaymentOptionId {
  Card = 6,
  Zip = 18,
  PayPal = 9,
  Alipay = 12,
  WeChat = 14,
  Bpay = 8,
  UnionPay = 15,
  AfterPay = 17
}

export enum PaymentOptionName {
  Card = 'creditcard',
  Zip = 'zip',
  PayPal = 'paypal',
  Alipay = 'alipay',
  WeChatPay = 'wechatpay',
  Bpay = 'bpay',
  UnionPay = 'unionpay',
  AfterPay = 'afterpay'
}

export const PaymentOptionNameToIdMap = new Map<PaymentOptionName, PaymentOptionId>();
PaymentOptionNameToIdMap.set(PaymentOptionName.Card, PaymentOptionId.Card);
PaymentOptionNameToIdMap.set(PaymentOptionName.Zip, PaymentOptionId.Zip);
PaymentOptionNameToIdMap.set(PaymentOptionName.PayPal, PaymentOptionId.PayPal);
PaymentOptionNameToIdMap.set(PaymentOptionName.Alipay, PaymentOptionId.Alipay);
PaymentOptionNameToIdMap.set(PaymentOptionName.WeChatPay, PaymentOptionId.WeChat);
PaymentOptionNameToIdMap.set(PaymentOptionName.Bpay, PaymentOptionId.Bpay);
PaymentOptionNameToIdMap.set(PaymentOptionName.UnionPay, PaymentOptionId.UnionPay);
PaymentOptionNameToIdMap.set(PaymentOptionName.AfterPay, PaymentOptionId.AfterPay);

export const AdyenTypeToPaymentOptionNameMap = new Map<AdyenPaymentMethodType, PaymentOptionName>();
AdyenTypeToPaymentOptionNameMap.set(AdyenPaymentMethodType.Card, PaymentOptionName.Card);
AdyenTypeToPaymentOptionNameMap.set(AdyenPaymentMethodType.Zip, PaymentOptionName.Zip);
AdyenTypeToPaymentOptionNameMap.set(AdyenPaymentMethodType.PayPal, PaymentOptionName.PayPal);
AdyenTypeToPaymentOptionNameMap.set(AdyenPaymentMethodType.Alipay, PaymentOptionName.Alipay);
AdyenTypeToPaymentOptionNameMap.set(AdyenPaymentMethodType.WeChat, PaymentOptionName.WeChatPay);
AdyenTypeToPaymentOptionNameMap.set(AdyenPaymentMethodType.UnionPay, PaymentOptionName.UnionPay);
AdyenTypeToPaymentOptionNameMap.set(AdyenPaymentMethodType.AfterPay, PaymentOptionName.AfterPay);
