import { CheckoutSession } from '@adyen/adyen-web/dist/types/types';
import { CheckoutInitData } from 'types/config';
import { PaymentOption } from 'types/paymentOptions';
import { apiPost } from 'utils/api';

type Session = {
  session: CheckoutSession;
  paymentOptions: PaymentOption[];
};

export const createSession = async (initData: CheckoutInitData): Promise<Session> => {
  const body = {
    paymentOptionsInput: {},
    ...initData
  };

  const { sessionResponse, paymentOptions } = await apiPost('/webapp/checkout/init', body);

  const session = {
    id: sessionResponse.sessionId,
    sessionData: sessionResponse.sessionData
  };

  return {
    session,
    paymentOptions
  };
};
