import { Box } from '@mui/material';
import { ReactNode } from 'react';

type MethodButtonContainerProps = {
  children: ReactNode;
};

const MethodButtonContainer = ({ children }: MethodButtonContainerProps): JSX.Element => {
  return <Box sx={{ mx: 'auto', maxWidth: ['100%', '300px'] }}>{children}</Box>;
};

export default MethodButtonContainer;
