import { createContext, useContext, useReducer, useCallback, useMemo } from 'react';
import Core from '@adyen/adyen-web/dist/types/core';
import { PaymentOptionName } from 'configurations/paymentOptions';
import { PaymentOption } from 'types/paymentOptions';

type State = {
  checkout: Core;
  paymentOptions: PaymentOption[];
  selectedMethod: PaymentOptionName | null;
};

type PaymentContextType = State & {
  setCheckout: (checkout: Core) => void;
  setPaymentOptions: (paymentOptions: PaymentOption[]) => void;
  setSelectedMethod: (selectedMethod: PaymentOptionName) => void;
};

type Action =
  | {
      type: 'SET_CHECKOUT';
      checkout: Core;
    }
  | {
      type: 'SET_PAYMENT_OPTIONS';
      paymentOptions: PaymentOption[];
    }
  | {
      type: 'SET_SELECTED_METHOD';
      selectedMethod: PaymentOptionName;
    };

const initialState: State = {
  checkout: {} as Core,
  paymentOptions: [],
  selectedMethod: null
};

export const PaymentContext = createContext<State | any>(initialState);
PaymentContext.displayName = 'PaymentContext';

const paymentReducer = (state: State, action: Action): State => {
  switch (action.type) {
    case 'SET_CHECKOUT':
      return {
        ...state,
        checkout: action.checkout
      };
    case 'SET_PAYMENT_OPTIONS':
      return {
        ...state,
        paymentOptions: action.paymentOptions
      };
    case 'SET_SELECTED_METHOD':
      return {
        ...state,
        selectedMethod: action.selectedMethod
      };
    default:
      return state;
  }
};

export const PaymentProvider = (props: any): JSX.Element => {
  const [state, dispatch] = useReducer(paymentReducer, initialState);

  const setCheckout = useCallback(
    (checkout: Core) => dispatch({ type: 'SET_CHECKOUT', checkout }),
    [dispatch]
  );

  const setPaymentOptions = useCallback(
    (paymentOptions: PaymentOption[]) => dispatch({ type: 'SET_PAYMENT_OPTIONS', paymentOptions }),
    [dispatch]
  );

  const setSelectedMethod = useCallback(
    (selectedMethod: PaymentOptionName) =>
      dispatch({ type: 'SET_SELECTED_METHOD', selectedMethod }),
    [dispatch]
  );

  const checkout = useMemo(() => state.checkout, [state.checkout]);
  const paymentOptions = useMemo(() => state.paymentOptions, [state.paymentOptions]);
  const selectedMethod = useMemo(() => state.selectedMethod, [state.selectedMethod]);

  const value = useMemo(
    () => ({
      checkout,
      paymentOptions,
      selectedMethod,
      setCheckout,
      setPaymentOptions,
      setSelectedMethod
    }),
    [checkout, paymentOptions, selectedMethod, setCheckout, setPaymentOptions, setSelectedMethod]
  );

  return <PaymentContext.Provider value={value} {...props} />;
};

export const usePaymentContext = () => {
  const context = useContext<PaymentContextType>(PaymentContext);

  if (context === undefined) {
    throw new Error(`usePaymentContext must be used within a PaymentProvider`);
  }

  return context;
};
