import { useEffect, useState } from 'react';
import { Skeleton, Stack, Alert, AlertTitle } from '@mui/material';
import { createCheckout } from 'services/createCheckout';
import { createSession } from 'services/createSession';
import { Card, Zip, PayPal, Alipay, WeChat, Bpay, UnionPay, AfterPay } from 'components/Methods';
import { ClientConfig } from 'types/config';
import { PaymentOptionName, PaymentOptionId } from 'configurations/paymentOptions';
import { getPaymentOptionNameToSelect, getVisiblePaymentOptions } from 'utils/paymentOptions';
import { usePaymentContext } from 'hooks/paymentContext';

type FormProps = {
  config: ClientConfig;
};

const Form = ({ config }: FormProps): JSX.Element => {
  const { checkout, paymentOptions, setCheckout, setPaymentOptions, setSelectedMethod } =
    usePaymentContext();
  const [initError, setInitError] = useState(false);

  useEffect(() => {
    const init = async () => {
      try {
        const { session, paymentOptions: sessionPaymentOptions } = await createSession(
          config.checkoutInitData
        );
        setPaymentOptions(sessionPaymentOptions);

        const checkout = await createCheckout(session, config);
        setCheckout(checkout);
        console.log('Checkout instance:\n', checkout);

        const defaultPaymentOption = getPaymentOptionNameToSelect(config, sessionPaymentOptions);

        if (defaultPaymentOption) {
          setSelectedMethod(defaultPaymentOption);
        }
      } catch (e) {
        console.error(e);
        setInitError(true);
      }
    };

    init();
  }, [config, setCheckout, setPaymentOptions, setSelectedMethod]);

  if (initError) {
    return (
      <Alert severity="error">
        <AlertTitle>Error</AlertTitle>
        Please reload the page and try again
      </Alert>
    );
  }

  if (Object.entries(checkout).length === 0) {
    return (
      <Stack spacing={1}>
        <Skeleton variant="rectangular" height={48} sx={{ borderRadius: 1 }} />
        <Skeleton variant="rectangular" height={144} sx={{ borderRadius: 1 }} />
        <Skeleton variant="rectangular" height={48} sx={{ borderRadius: 1 }} />
        <Skeleton variant="rectangular" height={48} sx={{ borderRadius: 1 }} />
      </Stack>
    );
  }

  if (!getVisiblePaymentOptions(paymentOptions)) {
    return <Alert severity="warning">No payment options are available</Alert>;
  }

  const handleSelectedMethod = (method: PaymentOptionName) => {
    setSelectedMethod(method);
  };

  const unsortedPaymentMethods = {
    [PaymentOptionId.Card]: <Card key={1} onAccordionChange={handleSelectedMethod} />,
    [PaymentOptionId.Zip]: <Zip key={2} onAccordionChange={handleSelectedMethod} />,
    [PaymentOptionId.PayPal]: <PayPal key={3} onAccordionChange={handleSelectedMethod} />,
    [PaymentOptionId.Alipay]: <Alipay key={4} onAccordionChange={handleSelectedMethod} />,
    [PaymentOptionId.WeChat]: <WeChat key={5} onAccordionChange={handleSelectedMethod} />,
    [PaymentOptionId.Bpay]: (
      <Bpay key={6} config={config} onAccordionChange={handleSelectedMethod} />
    ),
    [PaymentOptionId.UnionPay]: <UnionPay key={7} onAccordionChange={handleSelectedMethod} />,
    [PaymentOptionId.AfterPay]: <AfterPay key={8} onAccordionChange={handleSelectedMethod} />
  };

  return <>{paymentOptions.map(option => unsortedPaymentMethods[option.id])}</>;
};

export default Form;
