import {
  AdyenTypeToPaymentOptionNameMap,
  PaymentOptionName,
  PaymentOptionNameToIdMap
} from 'configurations/paymentOptions';
import { ClientConfig } from 'types/config';
import { PaymentOption } from 'types/paymentOptions';
import { PaymentClientError } from './errors';
import { isPaymentMethodEnabled } from './validation';

export const getVisiblePaymentOptions = (paymentOptions: PaymentOption[]): PaymentOption[] => {
  return paymentOptions.filter(option => !option.isHidden);
};

/**
 * Returns payment option name based on provided adyen callback state
 * @returns {PaymentOptionName} Payment option name on the client website
 */
export const derivePaymentOptionFromState = (state: any): PaymentOptionName => {
  const paymentOption = AdyenTypeToPaymentOptionNameMap.get(state.data.paymentMethod.type);

  if (!paymentOption) {
    throw new PaymentClientError(
      'Could not derive payment option name from the provided Adyen state'
    );
  }

  return paymentOption;
};

const getFirstAvailablePaymentOptionName = (
  paymentOptions: PaymentOption[]
): PaymentOptionName | null => {
  const visiblePaymentOptions = getVisiblePaymentOptions(paymentOptions);

  if (visiblePaymentOptions.length === 0) {
    return null;
  }

  return visiblePaymentOptions[0].name;
};

export const getPaymentOptionNameToSelect = (
  config: ClientConfig,
  paymentOptions: PaymentOption[]
): PaymentOptionName | null => {
  const { selectedPaymentOption } = config;

  if (!selectedPaymentOption) {
    return getFirstAvailablePaymentOptionName(paymentOptions);
  }

  const selectedPaymentOptionId = PaymentOptionNameToIdMap.get(selectedPaymentOption);

  if (!selectedPaymentOptionId) {
    return null;
  }

  if (!isPaymentMethodEnabled(paymentOptions, selectedPaymentOptionId)) {
    return getFirstAvailablePaymentOptionName(paymentOptions);
  }

  return selectedPaymentOption;
};
