import { useEffect, useRef, useState } from 'react';
import InfoOutlinedIcon from '@mui/icons-material/InfoOutlined';
import { Accordion, AccordionSummary, Box, Link } from '@mui/material';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import RedirectElement from '@adyen/adyen-web/dist/types/components/Redirect';
import { isAdyenPaymentMethodAvailable, isPaymentMethodEnabled } from 'utils/validation';
import { PaymentMethodProps } from 'types/components';
import { PaymentOptionId, PaymentOptionName } from 'configurations/paymentOptions';
import { AdyenPaymentMethodType } from 'configurations/adyenPaymentMethods';
import MethodNote from 'components/MethodUi/MethodNote';
import { PaymentActionEvent, usePaymentActionEvent } from 'hooks/usePaymentActionEvent';
import MethodHeader from 'components/MethodUi/MethodHeader';
import { MethodBody, MethodButtonContainer } from 'components/MethodUi';
import { usePaymentContext } from 'hooks/paymentContext';
import {
  PaymentComponentStatusEvent,
  usePaymentComponentStatusEvent
} from 'hooks/usePaymentComponentStatusEvent';

const AfterPay = ({ onAccordionChange }: PaymentMethodProps): JSX.Element | null => {
  const { checkout, paymentOptions, selectedMethod } = usePaymentContext();
  const componentRef = useRef<HTMLDivElement>(null);
  const [isEnabled, setIsEnabled] = useState(true);
  const [component, setComponent] = useState<RedirectElement>();

  useEffect(() => {
    const isMethodReady =
      isAdyenPaymentMethodAvailable(checkout, AdyenPaymentMethodType.AfterPay) &&
      isPaymentMethodEnabled(paymentOptions, PaymentOptionId.AfterPay);

    setIsEnabled(isMethodReady);

    if (isMethodReady && componentRef.current) {
      const element = checkout.create('afterpaytouch').mount(componentRef.current);
      setComponent(element);
    }
  }, [checkout, paymentOptions]);

  const handlePaymentActionEvent = (event: PaymentActionEvent) => {
    const { action } = event.detail;

    if (!(componentRef.current && isEnabled)) {
      return;
    }

    checkout.createFromAction(action).mount(componentRef.current);
  };

  const handleComponentStatusEvent = (event: PaymentComponentStatusEvent) => {
    const { status } = event.detail;

    if (!(component && isEnabled)) {
      return;
    }

    component.setStatus(status);
  };

  usePaymentActionEvent(PaymentOptionName.AfterPay, handlePaymentActionEvent);
  usePaymentComponentStatusEvent(PaymentOptionName.AfterPay, handleComponentStatusEvent);

  if (!isEnabled) {
    return null;
  }

  const isExpanded = selectedMethod === PaymentOptionName.AfterPay;

  return (
    <Accordion expanded={isExpanded} onChange={() => onAccordionChange(PaymentOptionName.AfterPay)}>
      <AccordionSummary
        expandIcon={<ExpandMoreIcon />}
        aria-controls="payment-afterpay-content"
        id="payment-afterpay-header"
      >
        <MethodHeader>Afterpay</MethodHeader>
      </AccordionSummary>
      <MethodBody>
        <MethodNote>
          You will be forwarded to the Afterpay portal to proceed with payment.
          <Box
            sx={{
              display: 'flex',
              justifyContent: 'center',
              p: 2
            }}
          >
            <Link
              href="/afterpay"
              target="_blank"
              underline="hover"
              sx={{
                display: 'flex',
                gap: 0.5,
                color: 'text.primary',
                fontWeight: 'bold',
                lineHeight: '1',
                alignItems: 'center'
              }}
            >
              <InfoOutlinedIcon color="info" />
              What Is Afterpay?
            </Link>
          </Box>
          Please note all Afterpay returns must be handled by our online Customer Service Team.
          Items must be returned to the Distribution Centre that your order has been sent from.
        </MethodNote>
        <MethodButtonContainer>
          <div ref={componentRef}></div>
        </MethodButtonContainer>
      </MethodBody>
    </Accordion>
  );
};

export default AfterPay;
