import { Suspense } from 'react';
import { createRoot, Root } from 'react-dom/client';
import '@adyen/adyen-web/dist/adyen.css';
import { PaymentClientError } from 'utils/errors';
import { ErrorBoundary, ErrorBoundaryFallback } from 'components/ErrorBoundary';
import Form from 'components/Form/Form';
import { ClientConfig } from 'types/config';
import { PaymentProvider } from 'hooks/paymentContext';
import clientActions from 'services/clientActions';

export class PaymentClientAdyen {
  constructor(private root: Root | null = null) {}

  static actions = clientActions;

  async render(selector: string, config: ClientConfig): Promise<void> {
    const container = document.querySelector(selector);

    if (!container) {
      throw new PaymentClientError('Mounting root was not found');
    }

    this.root = createRoot(container);

    this.root.render(
      <ErrorBoundary>
        <Suspense fallback={<ErrorBoundaryFallback />}>
          <PaymentProvider>
            <Form config={config} />
          </PaymentProvider>
        </Suspense>
      </ErrorBoundary>
    );
  }

  unmount(): void {
    if (!this.root) {
      return;
    }

    this.root.unmount();
  }
}

window.PaymentClientAdyen = PaymentClientAdyen;
