import { Box } from '@mui/material';
import { ReactNode } from 'react';

interface MethodNoteProps {
  children: ReactNode;
}

const MethodNote = ({ children }: MethodNoteProps): JSX.Element => {
  return (
    <Box sx={{ mb: 3, color: 'grey.700', fontSize: 14, textAlign: 'center', lineHeight: 1.4 }}>
      {children}
    </Box>
  );
};

export default MethodNote;
