import { useEffect, useRef, useState } from 'react';
import { Accordion, AccordionSummary, Box, Link } from '@mui/material';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import RedirectElement from '@adyen/adyen-web/dist/types/components/Redirect';
import { isAdyenPaymentMethodAvailable, isPaymentMethodEnabled } from 'utils/validation';
import { PaymentMethodProps } from 'types/components';
import { PaymentOptionId, PaymentOptionName } from 'configurations/paymentOptions';
import { AdyenPaymentMethodType } from 'configurations/adyenPaymentMethods';
import { MethodHeader, MethodNote, MethodBody, MethodButtonContainer } from 'components/MethodUi';
import { PaymentActionEvent, usePaymentActionEvent } from 'hooks/usePaymentActionEvent';
import { usePaymentContext } from 'hooks/paymentContext';
import {
  PaymentComponentStatusEvent,
  usePaymentComponentStatusEvent
} from 'hooks/usePaymentComponentStatusEvent';

const Zip = ({ onAccordionChange }: PaymentMethodProps): JSX.Element | null => {
  const { checkout, paymentOptions, selectedMethod } = usePaymentContext();
  const componentRef = useRef<HTMLDivElement>(null);
  const [isEnabled, setIsEnabled] = useState(true);
  const [component, setComponent] = useState<RedirectElement>();

  useEffect(() => {
    const isMethodReady =
      isAdyenPaymentMethodAvailable(checkout, AdyenPaymentMethodType.Zip) &&
      isPaymentMethodEnabled(paymentOptions, PaymentOptionId.Zip);

    setIsEnabled(isMethodReady);

    if (isMethodReady && componentRef.current) {
      const element = checkout.create('zip').mount(componentRef.current);
      setComponent(element);
    }
  }, [checkout, paymentOptions]);

  const handlePaymentActionEvent = (event: PaymentActionEvent) => {
    const { action } = event.detail;

    if (!(componentRef.current && isEnabled)) {
      return;
    }

    checkout.createFromAction(action).mount(componentRef.current);
  };

  const handleComponentStatusEvent = (event: PaymentComponentStatusEvent) => {
    const { status } = event.detail;

    if (!(component && isEnabled)) {
      return;
    }

    component.setStatus(status);
  };

  usePaymentActionEvent(PaymentOptionName.Zip, handlePaymentActionEvent);
  usePaymentComponentStatusEvent(PaymentOptionName.Zip, handleComponentStatusEvent);

  if (!isEnabled) {
    return null;
  }

  const isExpanded = selectedMethod === PaymentOptionName.Zip;

  return (
    <Accordion expanded={isExpanded} onChange={() => onAccordionChange(PaymentOptionName.Zip)}>
      <AccordionSummary
        expandIcon={<ExpandMoreIcon />}
        aria-controls="payment-zip-content"
        id="payment-zip-header"
      >
        <MethodHeader>Zip</MethodHeader>
      </AccordionSummary>
      <MethodBody>
        <MethodNote>
          You will be forwarded to the Zip portal to proceed with payment.
          <Box
            sx={{
              display: 'flex',
              justifyContent: 'center',
              p: 2
            }}
          >
            <Link
              href="/zip"
              target="_blank"
              underline="hover"
              sx={{
                display: 'flex',
                flexFlow: 'column',
                gap: 1,
                color: 'text.primary',
                fontWeight: 'bold'
              }}
            >
              <img
                src="https://static.chemistwarehouse.com.au/App_Themes/ams-cwh/Images/zip/zip-pay-logo.png"
                alt="Zip Payment"
              />
              What Is Zip?
            </Link>
          </Box>
          Please note all Zip returns must be handled by our online Customer Service Team. Items
          must be returned to the Distribution Centre that your order has been sent from.
        </MethodNote>
        <MethodButtonContainer>
          <div ref={componentRef}></div>
        </MethodButtonContainer>
      </MethodBody>
    </Accordion>
  );
};

export default Zip;
