import { useEffect, useState } from 'react';
import { Accordion, AccordionSummary } from '@mui/material';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import { isPaymentMethodEnabled } from 'utils/validation';
import { PaymentMethodProps } from 'types/components';
import { PaymentOptionId, PaymentOptionName } from 'configurations/paymentOptions';
import { MethodHeader, MethodBody, MethodButtonContainer } from 'components/MethodUi';
import PayButton from 'components/PayButton/PayButton';
import { ClientConfig } from 'types/config';
import { usePaymentContext } from 'hooks/paymentContext';

type BpayMethodProps = PaymentMethodProps & {
  config: ClientConfig;
};

const Bpay = ({ config, onAccordionChange }: BpayMethodProps): JSX.Element | null => {
  const { paymentOptions, selectedMethod } = usePaymentContext();
  const [isEnabled, setIsEnabled] = useState(true);

  useEffect(() => {
    const isMethodReady = isPaymentMethodEnabled(paymentOptions, PaymentOptionId.Bpay);

    setIsEnabled(isMethodReady);
  }, [paymentOptions]);

  const handleBpaySubmit = () => {
    config.onSubmit(null, null, PaymentOptionName.Bpay);
  };

  if (!isEnabled) {
    return null;
  }

  const isExpanded = selectedMethod === PaymentOptionName.Bpay;

  return (
    <Accordion expanded={isExpanded} onChange={() => onAccordionChange(PaymentOptionName.Bpay)}>
      <AccordionSummary
        expandIcon={<ExpandMoreIcon />}
        aria-controls="payment-bpay-content"
        id="payment-bpay-header"
      >
        <MethodHeader>BPAY</MethodHeader>
      </AccordionSummary>
      <MethodBody>
        <MethodButtonContainer>
          <PayButton onClick={handleBpaySubmit}>Place an orderasdasd</PayButton>
        </MethodButtonContainer>
      </MethodBody>
    </Accordion>
  );
};

export default Bpay;
