import { useEffect, useRef, useState } from 'react';
import { Accordion, AccordionSummary } from '@mui/material';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import CardElement from '@adyen/adyen-web/dist/types/components/Card';
import { PaymentMethodProps } from 'types/components';
import { PaymentOptionId, PaymentOptionName } from 'configurations/paymentOptions';
import { isAdyenPaymentMethodAvailable, isPaymentMethodEnabled } from 'utils/validation';
import { AdyenPaymentMethodType } from 'configurations/adyenPaymentMethods';
import { PaymentActionEvent, usePaymentActionEvent } from 'hooks/usePaymentActionEvent';
import MethodHeader from 'components/MethodUi/MethodHeader';
import { MethodBody } from 'components/MethodUi';
import { usePaymentContext } from 'hooks/paymentContext';
import {
  PaymentComponentStatusEvent,
  usePaymentComponentStatusEvent
} from 'hooks/usePaymentComponentStatusEvent';

const Card = ({ onAccordionChange }: PaymentMethodProps): JSX.Element | null => {
  const { checkout, paymentOptions, selectedMethod } = usePaymentContext();
  const componentRef = useRef<HTMLDivElement>(null);
  const [isEnabled, setIsEnabled] = useState(true);
  const [component, setComponent] = useState<CardElement>();

  useEffect(() => {
    const isMethodReady =
      isAdyenPaymentMethodAvailable(checkout, AdyenPaymentMethodType.Card) &&
      isPaymentMethodEnabled(paymentOptions, PaymentOptionId.Card);

    setIsEnabled(isMethodReady);

    if (isMethodReady && componentRef.current) {
      const element = checkout.create('card').mount(componentRef.current);
      setComponent(element);
    }
  }, [checkout, paymentOptions]);

  const handlePaymentActionEvent = (event: PaymentActionEvent) => {
    const { action } = event.detail;

    if (!(componentRef.current && isEnabled)) {
      return;
    }

    checkout.createFromAction(action).mount(componentRef.current);
  };

  const handleComponentStatusEvent = (event: PaymentComponentStatusEvent) => {
    const { status } = event.detail;

    if (!(component && isEnabled)) {
      return;
    }

    component.setStatus(status);
  };

  usePaymentActionEvent(PaymentOptionName.Card, handlePaymentActionEvent);
  usePaymentComponentStatusEvent(PaymentOptionName.Card, handleComponentStatusEvent);

  if (!isEnabled) {
    return null;
  }

  const isExpanded = selectedMethod === PaymentOptionName.Card;

  return (
    <Accordion expanded={isExpanded} onChange={() => onAccordionChange(PaymentOptionName.Card)}>
      <AccordionSummary
        expandIcon={<ExpandMoreIcon />}
        aria-controls="payment-card-content"
        id="payment-card-header"
      >
        <MethodHeader>Credit Card</MethodHeader>
      </AccordionSummary>
      <MethodBody>
        <div ref={componentRef}></div>
      </MethodBody>
    </Accordion>
  );
};

export default Card;
